   <CentrisModal v-if="modalOpened" @close="closeModal" />
  <div class="smm">
    <img class="smm-img" src="../assets/images/pageSmm/shadow.png" alt="">
    <img class="smm-imgMob" src="../assets/images/pageSmm/shadowMob.png" alt="">
    <div class="container">
      <div class="smm__head">
        <div class="smm__head-box">
          <img
            class="smm__head-box-img"
            src="../assets/images/pageSmm/c.png"
            alt=""
          />
          <h2 class="smm__head-box-title">
