              <p class="span-line">Выжать</p>
              Выжать
              <img
                class="sticker1"
                src="../assets/images/pageSmm/sticker1.png"
                alt=""
            /></span>
            <span
              >из соцсетей
              <img
                class="sticker2"
                src="../assets/images/pageSmm/sticker2.png"
                alt=""
            /></span>
            <span
              >максимум
              <img
                class="sticker3"
                src="../assets/images/pageSmm/sticker3.png"
                alt=""
            /></span>
          </h2>
          <CentrisBtn class="btn" @click="openModal" />
        </div>
      </div>
      <div class="smm__route">
        <div class="smm__route_content">
          <img class="smm__route_content-img" src="../assets/images/pageSmm/route.svg" alt="">
          <div class="circle">
            <div class="circle-content">
              <div class="circle-dot line">
                <span></span>
              </div>
              <div
                class="circle-dot"
                v-for="(item, index) in dotsItems"
                :key="index"
                :style="{
                  transform: `translate(-50%, -50%) rotateZ(${item.deg}deg)`,
                }"
              >
          
                <div class="circle-dot-items" :class="item.position">
                  <div
                    class="circle-dot-items-round"
                    @mouseover="showBox(index)"
                    @mouseleave="hideBox"
                  ></div>
                  <div
                    class="circle-dot-items-box"
                    :class="{ active: showTxt && showIndex == index }"
                    ref="routeDescr"
                    id="routeDescr"
                    :style="{
                      transform: `translate(-30%, -180%) rotateZ(${-item.deg}deg)`,
                    }"
                  >
                    <p class="circle-dot-items-descrBox">{{ item.txt }}</p>
                  </div>
                  <h4 class="circle-dot-items-title" v-html="item.title"></h4>
                </div>
           
              </div>
            </div>
          </div>
        </div>
        <p class="smm__route-descr">Маршрут действий</p>
      </div>
      <div class="smm__about">
        <div
          class="smm__about-item"
          v-for="(item, index) in smmItems"
          :key="index"
        >
          <h3 class="smm__about-item-title">{{ item.title }}</h3>
          <p class="smm__about-item-descr" v-html="item.descr"></p>
        </div>
      </div>
      <div class="smm__price">
        <h3 class="smm__price-title">стоимость пакета</h3>
        <p class="smm__price-span">от</p>
        <p
          class="smm__price-descr"
          v-intersection="counter"
          :class="{ active: count == result }"
        >
          {{ count }} 000 000
        </p>
        <p class="smm__price-sum">сум</p>
      </div>
    </div>
    <div class="wrapper">
      <div class="smm__line">
        <img
          class="smm__line-img"
          v-for="item in 10"
          :key="item"
          src="@/assets/images/logo.png"
          alt=""
        />
      </div>
    </div>
    <div class="smm__form">
      <div class="container">
        <div class="smm__form-box">
          <div class="smm__form-box-txt">
            <p class="smm__form-box-txt-descr">
              заказать <img src="../assets/images/pageSmm/fire.png" alt="" />
            </p>
            <p class="smm__form-box-txt-descr">бесплатный</p>
            <p class="smm__form-box-txt-descr">
              анализ <img src="../assets/images/pageSmm/arrow.png" alt="" />
            </p>
            <p class="smm__form-box-txt-descr">страницы</p>
          </div>
          <div class="smm__form-box-form">
            <form
              id="reg-course"
              class="modal__form"
              @submit.prevent
              @submit="sendFormData"
            >
              <div class="smm__form-box-form-input">
                <p class="smm__form-box-form-input-descr">Ваше имя</p>
                <input
                  type="text"
                  name="modal-name"
                  class="form__input"
                  required
                  autocomplete="off"
                  v-model="modalName"
                />
              </div>
              <div class="smm__form-box-form-input">
                <p class="smm__form-box-form-input-descr">номер телефона</p>
                <input
                  type="tel"
                  name="modal-tel"
                  class="form__input"
                  maxlength="17"
                  v-model="modalPhone"
                  @input="inputNumber"
                  @focus="focusNumber"
                  @blur="blurNumber"
                  required
                />
              </div>
              <div class="smm__form-box-form-input">
                <p class="smm__form-box-form-input-descr">
                  ваша имя в instagram
                </p>
                <input
                  type="text"
                  name="modal-name"
                  class="form__input"
                  v-model="modalUserName"
                />
              </div>
              <button
                class="modal-btn"
                v-if="!submitted"
                onclick="gtag('event', 'click', {'event_category': 'form'}); return true;"
              >
                Отправить заявку
              </button>
              <button class="modal-btn-accept" v-if="submitted">
                Отправлено ✔
              </button>
            </form>
          </div>
        </div>
      </div>
      <p class="smm__form-descr">
        Оставьте заявку и наши менеджера свяжутся с вами в ближайшее время
      </p>
    </div>
    <CentrisProjects class="smm__projects" id="projects"></CentrisProjects>
  </div>
  <CentrisFooter class="smm__footer" />
  <CentrisFooterSocial class="smm__footer-social" />
</template>

<script>
import CentrisBtn from "../components/modals/CentrisBtn.vue";
import CentrisModal from "@/components/modals/CentrisModal.vue";
import CentrisProjects from "../components/CentrisProjects.vue";
import CentrisFooter from "../components/CentrisFooter.vue";
import CentrisFooterSocial from "../components/CentrisFooterSocial.vue";
export default {
  data() {
    return {
      animationDuration: 300,
      count: 40,
      result: 4,
      submitted: false,
      modalName: "",
      modalPhone: "",
      modalUserName: "",
      showTxt: false,
      showIndex: 0,
      priceColor: false,
      modalOpened: false,
      smmItems: [
        {
          title: "SMM",
          descr:
            "стратегии продвижения <br> Ведение социальных сетей <br> Таргетированная реклама <br> Ситуативный контент <br> Комьюнити-менеджмент <br> копирайт отчётность",
        },
        {
          title: "PRODUCTION",
          descr: 
            "Видео / фото продакшн <br> Креатив <br> Съёмка рекламы <br> Имиджевое видео для соцсетей <br> анимация",
        },
        {
          title: "INFLUENCE",
          descr:
            "продвижения товаров и услуг <br> через блогеров и селебрити",
        },  
        {
          title: "PR ",
          descr:
            "Ведение корпоративных медиа <br> Мероприятия и комьюнити",
        },
      ],
      lineLogo: "logo.png",
      dotsItems: [
        {
          title: "Анализ <br> страницы",
          deg: -28,
          position: "top",
          txt: "Анализируем и выявляем сильные и слабые стороны",
        },
        {
          title: "Стратегия",
          deg: -18,
          position: "bottom",
          txt: "Намечаем дальнейшие планы и действия, учитывая запросы клиента",
        },
        {
          title: "Контент <br> план",
          deg: -10,
          position: "top",
          txt: "Составляем план публикаций на месяц, в зависимости от желаемых результатов",
        },
        {
          title: "Имидж",
          deg: -2,
          position: "bottom",
          txt: "Определяем сильную сторону бренда и делаем упор на неё, оформляя страницы в едином стиле",
        },
  
        {
          title: "Креатив",
          deg: 5,
          position: "top",
          txt: "Реализовываем креативные идеи, с учётом популярных трендов",
        },
        {
          title: "Реклама",
          deg: 15,
          position: "bottom",
          txt: "Таргетированная реклама, которая чётко настроена именно под вашу ЦА",
        },
        {
          title: "Конверсия",
          deg: 25,
          position: "top",
          txt: "Исходя из потребностей клиента делаем конверсию сайтов, Telegram-ботов, звонков и сообщений.",
        },
      ],
    };
  },
  methods: {
    counter() {
      setTimeout(() => {
        if (this.count > this.result) {
          this.count -= 1;
          this.counter();
        }
      }, this.animationDuration / this.result);
    },
    sendFormData() {
      if (
        this.modalName != "" &&
        this.modalPhone != "" &&
        this.modalPhone.length == 17
      ) {
        const formData = new FormData();
        formData.append("name", this.modalName);
        formData.append("username", this.modalUserName);
        formData.append("contact", this.modalPhone);
        this.submitted = true;
        this.$store.dispatch("sendForm", formData).then((response) => {
          this.modalName = "";
          this.modalPhone = "";
          this.modalUserName = "";
          this.submitted = true;
          setTimeout(() => {
            this.submitted = false;
            this.$emit("close");
          }, 2000);
        });
      }
    },
    inputNumber(event) {
      if (this.modalPhone.length < 5) this.modalPhone = "+998 ";
      this.modalPhone = this.modalPhone.replace(/[^+|^\d|^\s]/, "");
      const lengths = [4, 7, 11, 14];
      lengths.forEach((item) => {
        if (this.modalPhone.length == item && event.data) {
          this.modalPhone += " ";
        }
      });
    },
    focusNumber() {
      if (this.modalPhone == "") this.modalPhone = "+998 ";
    },
    blurNumber() {
      if (this.modalPhone == "+998 ") this.modalPhone = "";
    },
    showBox(index) {
      this.showIndex = index
      this.showTxt = true;
    },
    hideBox(){
      this.showTxt = false;
    },
    openModal() {
      this.modalOpened = true;
    },
    closeModal() {
      this.modalOpened = false;
    },
  },
  components: {
    CentrisBtn,
    CentrisModal,
    CentrisProjects,
    CentrisFooter,
    CentrisFooterSocial,
  },
};
</script>

